{
    margin: 0;
    padding: 0;
    font-family: poppins, sans-serif;
  
  }
  body{
    background-color: #c7d8e7;
  }
  
  a:link {
    color: green;
    background-color: transparent;
    text-decoration: none;
  }
  a:visited {
    background-color: transparent;
    text-decoration: none;
  }
  a:hover {
    color: red;
    background-color: transparent;
    text-decoration: underline;
  }
  a:active {
    color: yellow;
    background-color: transparent;
    text-decoration: underline;
  }
  
  .main-nav {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-60%, -60%);
    margin: 0;
    padding: 0;
    width: 800px;
    height: 200px;
  
  }
  
  .main-nav li {
    list-style: none;
    position: absolute;
    background: white;
    transition: 0.5s;
    margin: -70px;
    overflow: hidden;
    opacity: 0.8;
  }
  
  .main-nav li:hover {
    opacity: 1;
  }
  
  .main-nav li.item1 {
    top: 0;
    left: 0;
  }
  
  .main-nav li.item2 {
    bottom: 0;
    left: 25%;
  }
  
  .main-nav li.item3 {
    top: 0;
    left: 50%;
  }
  
  .main-nav li.item4 {
    bottom: 0;
    left: 75%;
  }
  
  .main-nav li.item5 {
    top: 0;
    left: 100%;
  }
  
  .main-nav li .bg {
    width: 100%;
    height: 100%;
    transform: scale(1.1);
  }
  
  .main-nav li.item1 .bg {
    /*background: url(../images/udemy.png);*/
    background-size: cover;
    background-position: center;
    border: 10px solid;
  }
  
  .main-nav li.item2 .bg  {
    /*background: url(../images/codeAcademy.png);*/
    background-size: cover;
    background-position: center;
    border: 10px solid;
  }
  
  .main-nav li.item3 .bg {
    /*background: url(../images/tutorialspoint.png);*/
    background-size: cover;
    background-position: center;
    border: 10px solid;
  }
  
  .main-nav li.item4 .bg {
    /*background: url(https://imgur.com/sz1BSkB.jpg);*/
    background-size: cover;
    background-position: center;
    border: 10px solid;
  }
  
  .main-nav li.item5 .bg {
    /*background: url(https://imgur.com/ara0hoV.jpg);*/
    background-size: cover;
    background-position: center;
    border: 10px solid #0d659d;
  }
  
  #center-page{
    text-align: center;
    color: #6BCAE2;
    background: #0D659D;
  }
  
  .textOverImage{
      width:200px;
      height:200px;
      display:inline-block;
      background-size:cover;
      border-radius:4px;
      margin:4px;
      position:relative;
      cursor:pointer;
      overflow:hidden;
    }
  
    .textOverImage > input{
      display:none;
    }
  
    .textOverImage > div,.textOverImage > h2{
      position:absolute;
      background-color:rgba(255,255,255,0.8);
      bottom:20px;
      left:4px;
      right:4px;
      border-radius:4px;
      padding:8px;
      overflow:hidden;
      opacity:0;
      transition:.3s;
    }
  
    .textOverImage:hover > h2,.textOverImage > input:checked + h2 + div{
      bottom:4px;
      opacity:1;
    }
  
    .textOverImage > input:checked + h2{
      bottom:-20px;
      opacity:0;
    }
  
    .textOverImage > h2{
      margin:0;
    }